.ton-button {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px auto;

  /* Зміщення вправо */
  transform: translateX(106%); /* Можеш змінити число на потрібне */
}

.ton-icon {
  width: 25%;
  height: 25%;
  transition: transform 0.1s ease;
}
