.sprite-button {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px auto;

  /* Зміщення вправо */
  transform: translateX(34%); /* Можеш змінити число на потрібне */
}

.sprite-icon {
  width: 30%;
  height: 30%;
  transition: transform 0.1s ease;
}
